import React from "react"
import { BrowserRouter, Switch, Route } from "react-router-dom"
import BadgeNew from "../pages/badgeNew"
import Badges from "../pages/badges"
import BadgeEdit from "../pages/badgeEdit"
import BadgeDetails from "../pages/badgeDetailsContainer"
import Layout from "./layout"
import NotFound from "../pages/notFound"
import Home from "../pages/Home"

const App = () => {
    return (

        <BrowserRouter>
            <Layout>
                <Switch>
                    <Route exact path="/Curso-React-JS-platzi-badges/" component={Home} />
                    <Route exact path="/Curso-React-JS-platzi-badges/badges" component={Badges} />
                    <Route exact path="/Curso-React-JS-platzi-badges/badges/new" component={BadgeNew} />
                    <Route exact path="/Curso-React-JS-platzi-badges/badges//:badgeId/edit" component={BadgeEdit} />
                    <Route exact path="/Curso-React-JS-platzi-badges/badges/:badgeId" component={BadgeDetails} />
                    <Route component={NotFound}/> {/**Esta ser칤a una p치gina de "Error404", si ning칰n elemento coincide con las rutas, tomar치 este componente de "error4" */}
                </Switch>
            </Layout>
        </BrowserRouter>
        // <BrowserRouter>
        //     <Layout>
        //         <Switch>
        //             <Route exact path="/" component={Home} />
        //             <Route exact path="/badges" component={Badges} />
        //             <Route exact path="/badges/new" component={BadgeNew} />
        //             <Route exact path="/badges/:badgeId/edit" component={BadgeEdit} />
        //             <Route exact path="/badges/:badgeId" component={BadgeDetails} />
        //             <Route component={NotFound}/> {/**Esta ser칤a una p치gina de "Error404", si ning칰n elemento coincide con las rutas, tomar치 este componente de "error4" */}
        //         </Switch>
        //     </Layout>
        // </BrowserRouter>
    )
}

export default App